<template>
  <div  :id="chartid" ref="echart" :style="{width:chartWidth,height: chartHeight+'px'}"></div>
</template>

<script>

import _ from 'lodash'
export default {
  name: "commonEcharts",
  props: {
    chartOption: {
      type: Object,
      default: () => {
      }
    },
    chartHeight:{
      type:String,
      default: "500"
    },
    chartWidth:{
      type:String,
      default: '800px'
    },
    // clickFun:{
    //   default:null,
    // },
  },
  data() {
    return {
      count: 1,
      chartid:null,
    }
  },
  beforeCreate() {
    this.chartid = _.uniqueId();
  },
  mounted() {
    this.drawChart();
  },
  watch: {
    chartOption: {
      handler: function () {
        this.drawChart();
      }
    },
  },
  methods: {
    /*
    
    http://t.csdn.cn/xHfhd
    */
    drawChart() {
      let that = this
      // let chartDom = document.getElementById(this.chartid);
      // let myChart = this.$echarts.init(chartDom);

      that.myChart =  this.$echarts.init(
          this.$refs.echart,'light'
      );

      // let chartDom = document.getElementById(this.chartid);
      // that.myChart = this.$echarts.init(
      //     chartDom,
      // );

      this.chartOption &&  that.myChart.setOption(this.chartOption);

      if(  this.chartOption && this.chartOption.zxcCallback || false){
        that.myChart.on('click',this.chartOption.zxcCallback);
      }
      // echartsresizeecharts
      window.addEventListener("resize", () => {
        // echartsresizeecharts
        that.myChart.resize();
        // echartsechartsresize,.vueecharts
        /*
        this.myChart2.resize();
        this.myChart3.resize();
        ......
        */
      });
    }
  },
  beforeDestroy() {
    /* resize
    CPUGPU*/
    window.removeEventListener("resize", () => {
      this.myChart.resize();
    });
  },
}
</script>

